import { clearUnreadTag, setConversationTargrt } from "actions/ui";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { RouteProps } from "react-router";
import { NavLink } from "react-router-dom";
import { AppState } from "stores";
import styled from "styled-components";

interface MessageListProps extends RouteProps {}

const MessageListContainer = styled.div`
  width: 300px;
  height: 100vh;
  position: fixed;
  left: 80px;
  top: 0px;
  border-right: 1px solid #dededf;
  z-index: 10;
`;

const MessageItem = styled(NavLink)`
  padding: 20px 15px;
  display: flex;
  cursor: pointer;
  transition: .2s all ease-out;
  position: relative;
  :hover { background: #dee0e3; }
  &.active { background: #f5f6f7; }
  &.unread::before {
    content: "";
    position: absolute;
    display: inline-block;
    width: 8px;
    height: 8px;
    background: #f00;
    border-radius: 50%;
    left: 50px;
    top: 20px;
    z-index: 15;
  }
`;

const MessageItemAvatar = styled.img`
  width: 45px;
  height: 45px;
  border-radius: 50%;
  position: relative;
`;

const MessageItemLabel = styled.div`
  width: 100%;
  margin: 0 10px;
  overflow: hidden;
  p { margin: 0; }
`;

const MessageItemName = styled.p`
  width: calc(100% - 25px);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #000;
`;

const MessageItemContent = styled.p`
  width: 100%;
  color: #888;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const MessageItemTime = styled.span`
  position: absolute;
  right: 10px;
  top: 20px;
  color: #888;
`;

export default function MessageList(props: MessageListProps) {
  const { recentMessages } = useSelector((state: AppState) => state.ui);
  const dispatch = useDispatch();

  const renderMessageList = () => {
    return Object.values(recentMessages)
      .sort((a: any, b: any) => b.priority - a.priority)
      .map((message: any, index) => {
        const sourceType = message.sourceType.replace('Message', '').toLowerCase();
        const sender = message.sourceType === 'GroupMessage' ? message.sender.group.id : message.sender.id;
        return (
          <MessageItem
            to={`/panel/message/${sourceType}/${sender}`}
            key={index}
            className={message.isRead ? "read" : "unread"}
            onClick={() => {
              dispatch(clearUnreadTag(message.id))
              dispatch(setConversationTargrt(message.sourceType, message.id))
            }}
          >
            <MessageItemAvatar src={message.avatar} alt="avatar" />
            <MessageItemLabel>
              <MessageItemName>{message.name}</MessageItemName>
              <MessageItemContent>{message.lastMessageStr}</MessageItemContent>
              <MessageItemTime>{moment(message.timestamp).format("HH:mm")}</MessageItemTime>
            </MessageItemLabel>
          </MessageItem>
        );
      });
  };

  return (
    <MessageListContainer>
      {renderMessageList()}
    </MessageListContainer>
  );
}