import { UPDATE_MESSAGE_RECORD } from "common/actionType";
import { MessageChain } from "common/MessageComponentType";
import { MessageTypeStr, SenderType } from "common/types";

export const updateMessageRecord = (sourceType: MessageTypeStr, target: number, sender: SenderType, messageChain: MessageChain) => {
  return {
    type: UPDATE_MESSAGE_RECORD,
    sourceType,
    target,
    sender,
    messageChain,
  };
}