import { put, call, takeLatest } from "@redux-saga/core/effects";
import Axios, { AxiosResponse } from "axios";

import { doLogin, setLoginFailed, setLoginSuccess } from "actions/auth";
import { DO_LOGIN } from "common/actionType";
import apiUrls from "common/api";

export function* login(action: ReturnType<typeof doLogin>) {
  try {
    const resp: AxiosResponse = yield call(Axios.post, apiUrls.login, {
      username: action.username,
      password: action.password,
    });

    if (resp.data.code === 0) {
      const payload = resp.data.payload;
      const { uid, username, authToken, expire } = payload;
      yield put(setLoginSuccess(uid, username, authToken, expire));
    } else {
      yield put(setLoginFailed(resp.data.code, resp.data.message));
    }
  } catch(err: any) {
    const resp = err.response;
    if (resp.status >= 400) {
      yield put(setLoginFailed(resp.data ? resp.data.code : resp.status, resp.data ? resp.data.message : ""));
    }
  }
}

export function* authSaga() {
  yield takeLatest(DO_LOGIN, login);
}