import {
  GET_BOT_ACCOUNT_INFO,
  GET_BOT_PUBLIC_INFO,
  SET_BOT_ACCOUNT_INFO,
  SET_BOT_PUBLIC_INFO,
} from "common/actionType";
import { Friend, Group, Profile } from "common/types";

export const getBotPublicInfo = () => ({ type: GET_BOT_PUBLIC_INFO });

export const setBotPublicInfo = (botName: string) => ({
  type: SET_BOT_PUBLIC_INFO,
  payload: {
    botName,
  },
});

export const getBotAccountInfo = (uid: number, authorization: string) => ({
  type: GET_BOT_ACCOUNT_INFO,
  uid,
  authorization,
});

export const setBotAccountInfo = (payload: {
  qq: number;
  profile: Profile;
  friendList: Friend[];
  groupList: Group[];
}) => ({
  type: SET_BOT_ACCOUNT_INFO,
  payload,
});
