import { MessageChain, MessageComponentTypeStr } from "common/MessageComponentType";
import { MessageTypeStr } from "common/types";
import moment from "moment";
import 'moment/locale/zh-cn';
import styled from "styled-components";
import { getUserAvatarUrl } from "utils/getAvatarUrl";

interface BubbleProps {
  id: number;
  name: string;
  time: number;
  type: MessageTypeStr;
  messageChain: MessageChain;
  lastId: number;
  showTag: boolean;
}

const BubbleContainer = styled.div`
  max-width: 50%;
  margin: 20px 10px 0 10px;
  display: flex;
  &.same {
    margin: 0;
  }
  &.same .container {
    margin-left: 60px;
    margin-top: 0;
    padding: 0;
  }
  &.same .content {
    border-radius: 5px 5px 5px 5px ;
  }
`;
const BubbleAvatar = styled.img`
  width: 40px;
  height: 40px;
  border-radius: 50%;
`;
const BubbleContentContainer = styled.div`
  width: 100%;
  margin-left: 10px;
`;
const BubbleSenderAndTime = styled.div`
  color: #888;
`;
const BubbbleTime = styled.span`
  margin-left: 10px;
  color: #ddd;
`;
const BubbleContent = styled.div`
  padding: 10px 15px;
  margin-top: 5px;
  width: auto;
  display: inline-block;
  background: #eff0f1;
  border-radius: 0px 5px 5px 5px ;
`;
const RecordImage = styled.img`
  max-width: 100%;
  display: block;
`;
const At = styled.span`
  color: #2f5ed3;
`;

const parseMessageChain = (messageChain: MessageChain) => {
  const components: JSX.Element[] = [];
  messageChain.forEach((item, index) => {
    if (typeof item === 'string') {
      components.push(<span key={index}>{item}</span>);
      return;
    }
    
    switch (item.type) {
      case MessageComponentTypeStr.PLAIN:
        return components.push(
          <span key={index}>{item.text}</span>
        );
      case MessageComponentTypeStr.IMAGE:
        return components.push(
          <RecordImage key={index} src={item.url!} alt={item.imageId!} referrerPolicy={"no-referrer"} />
        );
      case MessageComponentTypeStr.AT:
        return components.push(
          <At key={index}>@{item.target}</At>
        );
      case MessageComponentTypeStr.AT_ALL:
        return components.push(
          <At key={index}>@å…¨ä½“æˆå‘˜</At>
        );
      case MessageComponentTypeStr.FACE:
        return components.push(
          <span key={index}>[è¡¨æƒ…]</span>
        );
    }
  });
  return components;
};

export function Bubble(props: BubbleProps) {
  return (
    <BubbleContainer className={`${props.id === props.lastId ? "same" : ""}`}>
      {props.id !== props.lastId && (
        <BubbleAvatar src={getUserAvatarUrl(props.id)} alt={props.name} />
      )}
      <BubbleContentContainer className="container">
      {props.id !== props.lastId && (
        <BubbleSenderAndTime>
        {props.showTag && <span>{props.name}</span>}
        <BubbbleTime>{moment(props.time).locale("zh-cn").fromNow()}</BubbbleTime>
        </BubbleSenderAndTime>
      )}

        <BubbleContent className="content">
          {parseMessageChain(props.messageChain)}
        </BubbleContent>
      </BubbleContentContainer>
    </BubbleContainer>
  );
}