import { CLEAR_UNREAD_TAG, SET_CONVERSATION_TARGET, TOGGLE_SHOW_PROFILE_CARD, UPDATE_RECENT_MESSAGE_LIST } from "common/actionType";
import { MessageChain } from "common/MessageComponentType";
import { FriendMessageSender, GroupMessageSender, MessageTypeStr, SenderType } from "common/types";
import { getGroupAvatarUrl, getUserAvatarUrl } from "utils/getAvatarUrl";
import serializeMessage from "utils/serializeMessage";

export const toggleShowProfileCard = () => ({
  type: TOGGLE_SHOW_PROFILE_CARD,
});

export const updateRecentMessageList = (type: MessageTypeStr, sender: SenderType, messageChain: MessageChain) => {
  let name =
    type === "GroupMessage"
      ? (sender as GroupMessageSender).group.name
      : (sender as FriendMessageSender).remark
      ? (sender as FriendMessageSender).remark
      : (sender as FriendMessageSender).nickname;

  let avatar =
    type === "GroupMessage"
      ? getGroupAvatarUrl((sender as GroupMessageSender).group.id)
      : getUserAvatarUrl((sender as FriendMessageSender).id);

  let id = type === "GroupMessage" ? (sender as GroupMessageSender).group.id : (sender as FriendMessageSender).id;

  let messageStr = serializeMessage(messageChain);
  if (type === "GroupMessage")
    messageStr = `${(sender as GroupMessageSender).memberName}: ${messageStr}`;

  return {
    type: UPDATE_RECENT_MESSAGE_LIST,
    id,
    name,
    avatar,
    messageStr,
    sourceType: type,
    sender: sender,
  };
};


export const clearUnreadTag = (id: number) => ({
  type: CLEAR_UNREAD_TAG,
  id
})

export const setConversationTargrt = (conversationType: MessageTypeStr, target: number) => ({
  type: SET_CONVERSATION_TARGET,
  conversationType,
  target
});