import { Group } from "common/types";
import { useMemo } from "react";
import { useSelector } from "react-redux";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import { AppState } from "stores";
import styled from "styled-components";
import { getGroupAvatarUrl } from "utils/getAvatarUrl";

const GroupListContainer = styled.div`
  width: 300px;
  height: 100vh;
  position: fixed;
  left: 80px;
  top: 0px;
  border-right: 1px solid #dededf;
  z-index: 10;
  overflow-y: auto;
`;

const GroupItem = styled(Link)`
  width: 100%;
  display: flex;
  padding: 20px 10px;
  color: #000;
  &:hover {
    background: #dededf;
    transition: .2s all ease-out;
    cursor: pointer;
  }
`;

const GroupAvatar = styled.img`
  width: 50px;
  height: 50px;
  display: block;
  border-radius: 50%;
`;

const GroupLabel = styled.div`
  width: calc(100% - 70px);
  height: 50px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  
`;

const ContainerTitle = styled.div`
  text-align: center;
  padding: 15px 0;
  border-bottom: 1px solid #dededf;
`;

interface GroupListProps extends RouteComponentProps {}

export default function GroupList(props: GroupListProps) {
  const { groups } = useSelector((state: AppState) => state.bot);

  const renderFriends = useMemo(() => {
    if (!groups)
      return null;
    return groups.map((group: Group, index: number) => (
      <GroupItem key={index} to={`/panel/message/group/${group.id}`}>
          <GroupAvatar src={getGroupAvatarUrl(group.id)} alt={group.name} />
          <GroupLabel>
            {group.name}
          </GroupLabel>
      </GroupItem>
    ));
  }, [groups]);

  return (
    <GroupListContainer>
      <ContainerTitle>
        ç¾¤ç»„
      </ContainerTitle>
      {renderFriends}
    </GroupListContainer>
  );
}