import Navigator from "./Navigator";

import { Redirect, Route, RouteProps } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { AppState } from "stores";
import { useEffect } from "react";
import { getBotAccountInfo } from "actions/botInfo";
import { createWebsocketConnection } from "utils/websocket";

import MessageList from "./Message/MessageList";
import GroupMessagePage from './Message/GroupMessage';
import FriendMessagePage from "./Message/FriendMessage";
import FriendList from "./List/FriendList";
import GroupList from "./List/GroupList";

interface PanelPageProps extends RouteProps {}

export default function PanelPage(props: PanelPageProps) {
  const { uid, authorization } = useSelector((state: AppState) => state.bot);
  const dispatch = useDispatch();

  useEffect(() => {
    if (uid !== null && authorization !== null) {
      console.log(uid, authorization, dispatch);
      dispatch(getBotAccountInfo(uid, authorization));
      createWebsocketConnection(
        `${window.location.protocol.startsWith("https") ? "wss" : "ws"}://${window.location.host}/ws`,
        uid,
        authorization
      );
    }
  }, [uid, authorization, dispatch]);

  return (
    <div className="eine-panel">
      <Navigator />

      <Route path="/panel" exact>
        <Redirect to="/panel/message" />
      </Route>

      <Route path="/panel/message" component={MessageList} />
      <Route path="/panel/message/group/:id" component={GroupMessagePage} />
      <Route path="/panel/message/friend/:id" component={FriendMessagePage} />
      <Route path="/panel/friends" component={FriendList} />
      <Route path="/panel/groups" component={GroupList} />
    </div>
  );
}