import { UPDATE_MESSAGE_RECORD } from "common/actionType";
import { MessageChain } from "common/MessageComponentType";
import { MessageTypeStr, SenderType } from "common/types";

const initialState = {
  messageRecords: {}
};

export interface MessageRecordItem {
  sender: SenderType;
  messageChain: MessageChain;
  type: MessageTypeStr;
  time: number;
}

export interface MessageRecords {
  [key: number]: MessageRecordItem[];
}

export interface RecordReducerState {
  messageRecords: MessageRecords;
}

export default function RecordReducer(state: RecordReducerState = initialState, action: any) {
  let mergedObject: any = {};
  switch (action.type) {
    case UPDATE_MESSAGE_RECORD:
      mergedObject[action.target] = [].concat(
        (state.messageRecords[action.target] ? state.messageRecords[action.target].slice(-100) : []) as any,
        [
          {
            type: action.sourceType,
            sender: action.sender,
            messageChain: action.messageChain,
            time: +new Date(),
          },
        ] as any
      );
      return {
        ...state,
        messageRecords: {
          ...state.messageRecords,
          ...mergedObject,
        }
      }
    default:
      return state;
  }
}