import Axios, { AxiosResponse } from "axios";
import { call, put, takeLatest } from 'redux-saga/effects';

import apiUrls from "common/api";
import { setBotAccountInfo, setBotPublicInfo } from "actions/botInfo";
import { GET_BOT_ACCOUNT_INFO, GET_BOT_PUBLIC_INFO } from "common/actionType";
import { message } from "antd";

export function* fetchBotPublicInfo() {
  const result: AxiosResponse  = yield call(Axios.get, apiUrls.botPublicInfo);

  if (result.data.code === 0) {
    yield put(setBotPublicInfo(result.data.payload.botName));
  }
}

export function* fetchBotAccountInfo(action: any) {
  try {
    const resp: AxiosResponse = yield call(
      Axios.get,
      `${apiUrls.botAccountInfo}?uid=${action.uid}`,
      {
        headers: {
          authorization: action.authorization,
        },
      }
    );
    yield put(setBotAccountInfo(resp.data.payload));
  } catch(err: any) {
    const resp = err.response;
    message.error(`è·å–è´¦å·ä¿¡æ¯å¤±è´¥ï¼š${resp.data ? resp.data.message : resp.status}`);
  }
}

export function* botInfoSaga() {
  yield takeLatest(GET_BOT_PUBLIC_INFO, fetchBotPublicInfo);
  yield takeLatest(GET_BOT_ACCOUNT_INFO, fetchBotAccountInfo);
}
