import { Friend } from "common/types";
import { useMemo } from "react";
import { useSelector } from "react-redux";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import { AppState } from "stores";
import styled from "styled-components";
import { getUserAvatarUrl } from "utils/getAvatarUrl";

const FriendListContainer = styled.div`
  width: 300px;
  height: 100vh;
  position: fixed;
  left: 80px;
  top: 0px;
  border-right: 1px solid #dededf;
  z-index: 10;
  overflow-y: auto;
`;

const FriendItem = styled(Link)`
  width: 100%;
  display: flex;
  padding: 20px 10px;
  color: #000;
  &:hover {
    background: #dededf;
    transition: .2s all ease-out;
    cursor: pointer;
  }
`;

const FriendAvatar = styled.img`
  width: 50px;
  height: 50px;
  display: block;
  border-radius: 50%;
`;

const FriendLabel = styled.div`
  width: calc(100% - 70px);
  height: 50px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const ContainerTitle = styled.div`
  text-align: center;
  padding: 15px 0;
  border-bottom: 1px solid #dededf;
`;

interface FriendListProps extends RouteComponentProps {}

export default function FriendList(props: FriendListProps) {
  const { friends } = useSelector((state: AppState) => state.bot);

  const renderFriends = useMemo(() => {
    if (!friends)
      return null;
    return friends.map((friend: Friend, index: number) => (
      <FriendItem key={index} to={`/panel/message/friend/${friend.id}`}>
          <FriendAvatar src={getUserAvatarUrl(friend.id)} alt={friend.nickname} />
          <FriendLabel>
            {friend.remark ? friend.remark : friend.nickname}
          </FriendLabel>
      </FriendItem>
    ));
  }, [friends]);

  return (
    <FriendListContainer>
      <ContainerTitle>
        è”ç³»äºº
      </ContainerTitle>
      {renderFriends}
    </FriendListContainer>
  );
}