import { updateMessageRecord } from "actions/record";
import { updateRecentMessageList } from "actions/ui";
import { messageEventType } from "common/types";
import store from "stores";

let ws: WebSocket | null = null;
let token: string | undefined = undefined;

export const createWebsocketConnection = (url: string, uid: number, authorization: string) => {
  ws = new WebSocket(url);
  
  ws.addEventListener('open', () => sendViaWebsocket({
    type: "authenticate",
    uid,
    authorization,
  }));

  ws.addEventListener('message', (event) => resolveWebsocketMessage(event.data))
}

export const resolveWebsocketMessage = (rawMessage: string) => {
  let message: any;
  try {
    message = JSON.parse(rawMessage);
  } catch(err) {
    console.error(err);
  }

  if (message.type === 'AuthSuccess') {
    token = message.token;
    console.log("[ws] Auth success, token = ", token);
    return;
  }
  if (message.type === 'RefreshToken') {
    token = message.token;
    console.log('[ws] Refresh token, token = ', token);
    return;
  }

  if (messageEventType.includes(message.type)) {
    const { type, sender, messageChain } = message;
    console.log("receive message");
    store.dispatch(updateRecentMessageList(type, sender, messageChain));
    store.dispatch(updateMessageRecord(
      type,
      (type === 'GroupMessage' ? sender.group.id : sender.id),
      sender,
      messageChain
    ));
    return;
  }
};

export const sendViaWebsocket = <P extends object>(payload: P | string) => {
  if (!ws)
    return false;
  
  if (typeof payload === 'string') {
    ws.send(payload);
    return;
  }

  ws.send(JSON.stringify({
    ...payload,
    token,
  }));
};