import { updateMessageRecord } from "actions/record";
import { updateRecentMessageList } from "actions/ui";
import axios from "axios";
import { MessageComponentTypeStr, Plain } from "common/MessageComponentType";
import { MessageTypeStr } from "common/types";
import React from "react";
import store from "stores";
import styled from "styled-components";

const InputBoxContainer = styled.textarea`
  width: calc(100% - 60px);
  height: 44px;
  padding: 10px;
  border: 1px solid #dededf;
  outline: 0;
  margin: 10px 30px;
  border-radius: 5px;
  resize: none;
`;

export interface InputBoxProps {
  name: string;
  type: MessageTypeStr;
  target: number;
  group?: number

  authorization: string;
  uid: number;
}

export default function InputBox(props: InputBoxProps) {
  const textarea = React.createRef<HTMLTextAreaElement>();

  const { uid, authorization, target, type, name } = props;
  const onKeydown = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (event.key === "Enter") {
      event.preventDefault();
      const sendContent = (event.target as HTMLTextAreaElement).value;
      if (!sendContent.length) {
        return;
      }
      axios.post('/api/send_text', {
        uid,
        authorization,
        text: sendContent,
        target,
        type,
      })
        .then(res => {
          if (textarea.current)
            textarea.current.value = "";
          const sender = {
            id: store.getState().bot.qq,
            memberName: store.getState().bot.profile.nickname,
            nickname: name,
            group: {
              name,
              id: target,
            }
          } as any;
          const messageChain = [
            {
              type: MessageComponentTypeStr.PLAIN,
              text: sendContent,
            } as Plain,
          ];
          store.dispatch(
            updateMessageRecord(
              type,
              target,
              sender,
              messageChain,
            )
          );
          store.dispatch(updateRecentMessageList(type, {
            ...sender,
            id: target,
          }, messageChain));
        });
    }
  };
  
  return (
    <InputBoxContainer placeholder={`å‘é€ç»™ ${props.name}`} onKeyDown={onKeydown} ref={textarea} />
  );
}