import axios from "axios";
import { Friend, FriendMessageSender, MessageTypeStr, SenderType } from "common/types";
import React from "react";
import { useEffect, useMemo, useState } from "react";
import { useSelector } from "react-redux";
import { RouteComponentProps } from "react-router";
import { MessageRecordItem } from "reducers/RecordReducer";
import { AppState } from "stores";
import styled from "styled-components";
import { getUserAvatarUrl } from "utils/getAvatarUrl";
import { Bubble } from "../common/Bubble";
import InputBox from "../common/InputBox";

interface FriendMessagePageProps extends RouteComponentProps {}

const FriendMessageContainer = styled.div`
  width: calc(100vw - 380px);
  position: fixed;
  left: 380px;
`;

const FriendInfoContainer = styled.div`
  padding: 20px 30px;
  display: flex;
  border-bottom: 1px solid #dededf;
`;

const FriendAvatar = styled.img`
  width: 45px;
  height: 45px;
  border-radius: 50%;
`;

const FriendLabel = styled.div`
  width: 100%;
  margin-left: 10px;
`;

const FriendNameLabel = styled.p`
  font-size: 16px;
  font-weight: bold;
  margin: 0;
`;

const FriendSignLabel = styled.p`
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 0;
  color: #90959d;
  svg { margin-right: 5px; }
`;

const FriendRecordContainer = styled.div`
  padding: 5px 25px;
  height: calc(90vh - 60px);
  overflow: auto;
`;

export default function FriendMessagePage(props: FriendMessagePageProps) {
  const { friends, uid, authorization } = useSelector((state: AppState) => state.bot);
  const { messageRecords } = useSelector((state: AppState) => state.record);

  const friendId = (props.match.params as any).id;
  const friendItem = friends.filter((item: Friend) => item.id === Number(friendId));

  const [friendInfo, setFriendInfo] = useState<any | null>(friendItem.length ? friendItem[0] : null);

  const recordsContainerRef = React.createRef<HTMLDivElement>();

  const renderRecords = useMemo(() => {
    if (!messageRecords[friendId])
      return;

    const getSenderName = (sender: SenderType, type: MessageTypeStr) => {
      const tmp = (sender as FriendMessageSender);
      return tmp.remark || tmp.nickname;
    }

    return messageRecords[friendId].map((message: MessageRecordItem, index: number) => (
      <Bubble
        key={index}
        id={message.sender.id}
        name={getSenderName(message.sender, message.type)}
        time={message.time}
        type={message.type}
        showTag={false}
        messageChain={message.messageChain}
        lastId={index > 0 ? messageRecords[friendId][index-1].sender.id : -1}
      />
    ));
  }, [friendId, messageRecords]);

  useEffect(() => {
    (async () => {
      await axios
        .get(`/api/friend_info?uid=${uid}&friendId=${friendId}`, {
          headers: { authorization },
        })
        .then((res) => {
          if (res.data.code === 0) setFriendInfo(Object.assign({}, friendInfo, res.data.payload));
        });
    })();
  // eslint-disable-next-line
  }, [friends, friendId, uid, authorization]);

  if (!friendInfo) {
    return null;
  }


  return (
    <FriendMessageContainer>
      <FriendInfoContainer>
        <FriendAvatar src={getUserAvatarUrl(friendId)} alt={friendInfo.nickname} />
        <FriendLabel>
          <FriendNameLabel>
            {(friendInfo.remark ? friendInfo.remark : friendInfo.nickname)}
          </FriendNameLabel>
          <FriendSignLabel>
            {friendInfo.sign}
          </FriendSignLabel>
        </FriendLabel>
      </FriendInfoContainer>
      <FriendRecordContainer ref={recordsContainerRef}>
        {renderRecords}
      </FriendRecordContainer>
      <InputBox
        uid={uid}
        authorization={authorization}
        name={friendInfo.nickname ? friendInfo.nickname : friendInfo.remark}
        type={MessageTypeStr.FRIEND_MESSAGE}
        target={friendId}
      />
    </FriendMessageContainer>
  );
}