export default function ProfileCard(props: ProfileCardProps) {
  const { qq, profile, botName } = useSelector((state: AppState) => state.bot);
  const dispatch = useDispatch();

  const handleBackstageClick = (e: React.MouseEvent<HTMLDivElement>) => {
    dispatch(toggleShowProfileCard());
  };
  const preventPropagation = (e: React.MouseEvent<HTMLDivElement>) => {
    e.stopPropagation();
  }
  const doLogout = () => {
    message.info("æ­£åœ¨é€€å‡ºå½“å‰è´¦å·â€¦â€¦");

    localStorage.removeItem("eine_uid");
    localStorage.removeItem("eine_username");
    localStorage.removeItem("eine_authorization");
    localStorage.removeItem("eine_expire");

    setTimeout(() => window.location.href = "/login", 1000);
  }
  
  return (
    <ProfileCardBackstage onClick={handleBackstageClick}>
      <ProfileCardContainer onClick={preventPropagation}>
        <BaseProfile>
          <BaseProfileAvatar src={getUserAvatarUrl(qq)} alt="avatar" />
          <BaseProfileNameContainer>
            <BaseProfileNickname>{profile ? profile.nickname : null}</BaseProfileNickname>
            <BaseProfileBotName>BOT åç§°ï¼š{botName}</BaseProfileBotName>
          </BaseProfileNameContainer>
        </BaseProfile>
        <BaseProfileSign>
          <MessageEmoji theme="filled" size="16" fill="#90959d"/>
          <span>{profile ? profile.sign : null}</span>
        </BaseProfileSign>
        <LogoutButton onClick={doLogout}>
          <Logout theme="filled" size="16" fill="#90959d"/>
          <span>é€€å‡ºç™»å½•</span>
        </LogoutButton>
      </ProfileCardContainer>
    </ProfileCardBackstage>
  );
}