import { DO_LOGIN, SET_LOGIN_FAILED, SET_LOGIN_SUCCESS } from "common/actionType";

export const doLogin = (username: string, password: string) => ({
  type: DO_LOGIN,
  username,
  password,
});

export const setLoginSuccess = (
  uid: number,
  username: string,
  authToken: string,
  expire: number
) => ({
  type: SET_LOGIN_SUCCESS,
  uid,
  username,
  authToken,
  expire,
});

export const setLoginFailed = (code: number, message: string) => ({
  type: SET_LOGIN_FAILED,
  code,
  message
});
