import { CLEAR_UNREAD_TAG, SET_CONVERSATION_TARGET, TOGGLE_SHOW_PROFILE_CARD, UPDATE_RECENT_MESSAGE_LIST } from "common/actionType";
import { MessageTypeStr, SenderType } from "common/types";

const initialState = {
  showProfileCard: false,
  recentMessages: {},

  currentConversationType: MessageTypeStr.FRIEND_MESSAGE,
  currentConversationTarget: 0,
};

export interface RecentMessageItem {
  id: number;
  name: string;
  avatar: string;
  priority: number;
  timestamp: number;
  lastMessageStr: string;
  isRead: boolean;
  sourceType: MessageTypeStr;
  sender: SenderType;
}

export interface UIReducerState {
  showProfileCard: boolean;
  recentMessages: {
    [key: number]: RecentMessageItem;
  };
  currentConversationType: MessageTypeStr;
  currentConversationTarget: number;
}

export default function UIReducer(state = initialState, action: any) {
  let mergeObjects: { [key: number]: RecentMessageItem } = {};

  switch (action.type) {
    case TOGGLE_SHOW_PROFILE_CARD:
      return {
        ...state,
        showProfileCard: !state.showProfileCard,
      };

    case UPDATE_RECENT_MESSAGE_LIST:
      console.log(action.id === state.currentConversationTarget);
      mergeObjects[action.id] = {
        id: action.id,
        name: action.name,
        avatar: action.avatar,
        priority: +new Date(),
        timestamp: +new Date(),
        lastMessageStr: action.messageStr,
        isRead: action.id === state.currentConversationTarget,
        sourceType: action.sourceType,
        sender: action.sender,
      };
      return {
        ...state,
        recentMessages: {
          ...state.recentMessages,
          ...mergeObjects
        }
      };

    case CLEAR_UNREAD_TAG:
      mergeObjects = {};
      mergeObjects[action.id] = {
         ...(state.recentMessages as any)[action.id],
         isRead: true
      };
      return {
        ...state,
        recentMessages: {
          ...state.recentMessages,
          ...mergeObjects,
        }
      };

    case SET_CONVERSATION_TARGET:
      return {
        ...state,
        currentConversationType: action.conversationType,
        currentConversationTarget: action.target,
      };

    default:
      return state;
  }
}
