import { DO_LOGIN, SET_BOT_ACCOUNT_INFO, SET_BOT_PUBLIC_INFO, SET_LOGIN_FAILED, SET_LOGIN_SUCCESS } from "common/actionType";
import { LoginStatus } from "common/constant";
import { Friend, Group, Profile } from "common/types";
import { getPreservedAuthorizationInfo } from "utils/getPreservedAuthorizationInfo";

// get preserved login state from localStorage
const initialAuthorizationInfo = getPreservedAuthorizationInfo();

const initialState = {
  // bot public info
  botName: "Eine",

  // login status
  loginStatus: LoginStatus.NOT_LOGIN,
  loginErrorCode: 0,
  loginErrorMessage: "",
  ...initialAuthorizationInfo,

  // bot account info,
  qq: 0,
  profile: null,
  friends: [],
  groups: [],
};

export interface BotReducerProps {
  authorization: string | null;
  botName: string;
  expire: number;
  friends: Friend[];
  groups: Group[];
  loginErrorCode: number;
  loginErrorMessage: string;
  loginStatus: LoginStatus;
  qq: number;
  profile: Profile | null;
  uid: number | null;
  username: string | null;
}

export default function BotReducer(state: BotReducerProps = initialState, action: any) {
  switch (action.type) {
    case SET_BOT_PUBLIC_INFO:
      return {
        ...state,
        ...action.payload,
      };

    case SET_LOGIN_SUCCESS:
      localStorage.setItem("eine_uid", action.uid);
      localStorage.setItem("eine_username", action.username);
      localStorage.setItem("eine_authorization", action.authToken);
      localStorage.setItem("eine_expire", action.expire);
      return {
        ...state,
        uid: action.uid,
        username: action.username,
        authorization: action.authToken,
        expire: action.expire,
        loginStatus: LoginStatus.LOGIN_SUCCESS,
        loginErrorCode: 0,
        loginErrorMessage: "",
      };

    case SET_LOGIN_FAILED:
      return {
        ...state,
        loginStatus: LoginStatus.LOGIN_FAILED,
        loginErrorCode: action.code,
        loginErrorMessage: action.message,
      };

    case DO_LOGIN:
      return {
        ...state,
        loginStatus: LoginStatus.NOT_LOGIN,
      };

    case SET_BOT_ACCOUNT_INFO:
      return {
        ...state,
        qq: action.payload.qq,
        profile: action.payload.profile,
        friends: action.payload.friendList,
        groups: action.payload.groupList,
      };

    default:
      return state;
  }
}
