import { Peoples, Clipboard } from "@icon-park/react";
import axios from "axios";
import { FriendMessageSender, Group, GroupMessageSender, MessageTypeStr, SenderType } from "common/types";
import React from "react";
import { useEffect, useMemo, useState } from "react";
import { useSelector } from "react-redux";
import { RouteComponentProps } from "react-router";
import { MessageRecordItem } from "reducers/RecordReducer";
import { AppState } from "stores";

import styled from "styled-components";
import { getGroupAvatarUrl } from "utils/getAvatarUrl";
import { Bubble } from "../common/Bubble";
import InputBox from "../common/InputBox";

interface GroupMessagePageProps extends RouteComponentProps {}

const GroupMessageContainer = styled.div`
  width: calc(100vw - 380px);
  position: fixed;
  left: 380px;
`;

const GroupInfoContainer = styled.div`
  padding: 20px 30px;
  display: flex;
  border-bottom: 1px solid #dededf;
`;

const GroupAvatar = styled.img`
  width: 45px;
  height: 45px;
  border-radius: 50%;
`;

const GroupInfoLabel = styled.div`
  width: 100%;
  margin-left: 10px;
`;

const GroupNameLabel = styled.p`
  font-size: 16px;
  font-weight: bold;
  margin: 0;
`;

const GroupConfigLabel = styled.p`
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 0;
  color: #90959d;
  svg { margin-right: 5px; }
`;

const SplitLine = styled.span`
  padding: 0 10px;
`;

const GroupRecordContainer = styled.div`
  padding: 5px 25px;
  height: calc(90vh - 60px);
  overflow: auto;
`;

export default function GroupMessagePage(props: GroupMessagePageProps) {
  const { groups, uid, authorization } = useSelector((state: AppState) => state.bot);
  const { messageRecords } = useSelector((state: AppState) => state.record);

  const groupId = Number((props.match.params as any).id);
  const groupItem = groups.filter((item: Group) => item.id === groupId);  
  const [groupInfo, setGroupInfo] = useState<any | null>(groupItem.length ? groupItem[0] : null);
  const [groupConfig, setGroupConfig] = useState<any | null>(null);
  const recordsContainerRef = React.createRef<HTMLDivElement>();

  const renderRecords = useMemo(() => {
    if (!messageRecords[groupId])
      return;

    const getSenderName = (sender: SenderType, type: MessageTypeStr) => {
      if (type === 'GroupMessage')
        return (sender as GroupMessageSender).memberName;
      const tmp = (sender as FriendMessageSender);
      return tmp.remark || tmp.nickname;
    }

    return messageRecords[groupId].map((message: MessageRecordItem, index: number) => (
      <Bubble
        key={index}
        id={message.sender.id}
        name={getSenderName(message.sender, message.type)}
        time={message.time}
        type={message.type}
        messageChain={message.messageChain}
        showTag={true}
        lastId={index > 0 ? messageRecords[groupId][index-1].sender.id : -1}
      />
    ));
  }, [groupId, messageRecords]);

  useEffect(() => {
    (async() => {
      const groupItem = groups.filter((item: Group) => item.id === groupId);

      await axios.get(`/api/group_info?uid=${uid}&groupId=${groupId}`, {
        headers: { authorization }
      }).then(res => setGroupConfig(res.data.payload));

      setGroupInfo(groupItem.length ? groupItem[0] : null);
    })();
  }, [groups, groupId, uid, authorization]);

  useEffect(() => {
    if (!recordsContainerRef.current)
      return;
      recordsContainerRef.current.scrollTop = recordsContainerRef.current?.scrollHeight;
  // eslint-disable-next-line
  }, [groupId, messageRecords]);

  if (!groupInfo) {
    return null;
  }

  return (
    <GroupMessageContainer>
      <GroupInfoContainer>
        <GroupAvatar src={getGroupAvatarUrl(groupInfo.id)} alt={groupInfo.name} />
        <GroupInfoLabel>
          <GroupNameLabel>{groupInfo && groupInfo.name}</GroupNameLabel>
          <GroupConfigLabel>
            <span>
              <Peoples theme="filled" size="12" fill="#90959d" />
              <span>{groupConfig && groupConfig.members.length}</span>
            </span>
            <SplitLine>|</SplitLine>
            <span>
              <Clipboard theme="filled" size="12" fill="#90959d" />
              <span>{groupConfig && groupConfig.announcement}</span>
            </span>
          </GroupConfigLabel>
        </GroupInfoLabel>
      </GroupInfoContainer>

      <GroupRecordContainer ref={recordsContainerRef}>{renderRecords}</GroupRecordContainer>

      <InputBox
        uid={uid}
        authorization={authorization}
        name={groupInfo.name}
        type={MessageTypeStr.GROUP_MESSAGE}
        target={groupInfo.id}
      />
    </GroupMessageContainer>
  );
}