import { AuthorizatoinInfo } from "common/types";

export const getPreservedAuthorizationInfo = () => {
  const preservedExpire =  localStorage.getItem("eine_expire");
  const currentTimestamp = +new Date();
  const usePreservedAuthorizationInfo = preservedExpire && Number(preservedExpire) > currentTimestamp;
  let uid;
  let initialAuthorizationInfo: AuthorizatoinInfo = {
    uid: (usePreservedAuthorizationInfo && (uid = localStorage.getItem("eine_uid"))) ?  Number(uid) :  null,
    username: usePreservedAuthorizationInfo ? localStorage.getItem("eine_username") : null,
    authorization: usePreservedAuthorizationInfo ? localStorage.getItem("eine_authorization") : null,
    expire: Number(preservedExpire),
  };

  if (!initialAuthorizationInfo.uid || !initialAuthorizationInfo.username || !initialAuthorizationInfo.authorization) {
    initialAuthorizationInfo = {
      uid: null,
      username: null,
      authorization: null,
      expire: 0,
    };
  };
  return initialAuthorizationInfo;
}